import { makeStyles } from "@material-ui/core";

export default makeStyles((theme) => ({
    root: {
        flex: 1,
        justifyContent: 'center',
        alignItems: 'center',
        minHeight: "100vh",
        height: "100vh",
        // backgroundColor: blue,
    },
    toolbar: theme.mixins.toolbar,
    content: {
        flexGrow: 1,
        backgroundColor: theme.palette.background.default,
        padding: theme.spacing(3)
    },
    // header: {
    //     textAlign: 'center',
    //     alignItems: 'center',
    //     height: '100vh',
    //     width: '100%', //'135.95em',
    //     // height: '100em',
    //     // width: '135.95em',
    // },
    headerContainer: {
        flex: 1,
        height: '100vh',
        overflow: 'hidden',
        color: '#fff',
        textAlign: 'left',
        alignContent: 'center',
        justifyContent: 'center',
        paddingLeft: '10em',
        paddingRight: '10em',
        paddingTop: '25em',
        [theme.breakpoints.down("lg")]: {
            height: '100vh',
            paddingLeft: '7em',
            paddingRight: '7em',
        },
        [theme.breakpoints.down("md")]: {
            height: '100vh',
            paddingLeft: '7em',
            paddingRight: '7em',
            paddingTop: '30vh',
        },
        [theme.breakpoints.down("sm")]: {
            height: '35em',
            paddingLeft: '3em',
            paddingRight: '3em',
            paddingTop: '11em',
            minHeight: '25em',
        },
        [theme.breakpoints.down("xs")]: {
            height: '25em',
            paddingLeft: '3em',
            paddingRight: '3em',
            paddingTop: '7em',
            minHeight: '25em',
        },
    },
    darkBG: {
        backgroundColor: 'rgba(0, 0, 0, 0.17)'
    },
    headerTitle: {
        fontFamily: 'Podkova',
        fontSize: "8rem",
        [theme.breakpoints.down("lg")]: {
            fontSize: "8rem"
        },
        [theme.breakpoints.down("md")]: {
            fontSize: "7rem"
        },
        [theme.breakpoints.down("sm")]: {
            fontSize: "4rem"
        },
        [theme.breakpoints.down("xs")]: {
            fontSize: "3rem"
        }
    },
    headerTitle2: {
        fontFamily: 'Podkova',
        fontSize: "5rem",
        [theme.breakpoints.down("lg")]: {
            fontSize: "5rem"
        },
        [theme.breakpoints.down("md")]: {
            fontSize: "4rem"
        },
        [theme.breakpoints.down("sm")]: {
            fontSize: "2.5rem"
        },
        [theme.breakpoints.down("xs")]: {
            fontSize: "2rem"
        }
    },
    headerSubtitle: {
        fontSize: "1.5rem",
        [theme.breakpoints.down("lg")]: {
            fontSize: "1.5rem"
        },
        [theme.breakpoints.down("md")]: {
            fontSize: "1rem"
        }
    },
    about: {
        height: '32em',
        textAlign: 'center',
        padding: '7em',
        backgroundColor: 'rgb(201,240,241)',
        background: 'linear-gradient(180deg, rgba(194,246,249,0.6) 7%, rgba(241,251,251,0.75) 48%, rgba(200,250,252,0.60) 94%)'
    },
    products: {

    }
}));